<template>
	<div class="ordering" v-loading.fullscreen.lock="loading">
		<div class="column">
			<div class="columnPlot">
				<div class="heading">最近七天销售额</div>
				<div class="house-view">
					<div id="container"></div>
				</div>
			</div>
			<div class="piePlot">
				<div class="heading">菜品销量排行榜</div>
				<div class="house_plot">
					<div id="container_one"></div>
				</div>
			</div>
		</div>
		<div class="bottom_line">
			<div class="house_line">
				<div></div>
				<div id="container_two"></div>
			</div>
		</div>
		
		
	</div>
</template>

<script>
import { Column, Line,Pie } from '@antv/g2plot';
export default {
	data() {
		return {
			loading: true
		};
	},
	methods: {
		async seven_days() {
			try {
				// 柱状图数据
				let res = await new this.Request(this.Urls.m().salesvolume).modeget();
				console.log(res.data.data)
				// 饼图数据
				let res1 =await new this.Request(this.Urls.m().salesPlot).modeget();
				console.log(res1.data.data);
				this.house(res.data.data);
				this.house_view(res1.data.data)
				this.loading = false;
			} catch (e) {
				new this.mytitle(this.$message, 'error', '服务器发生错误,请重试').funtitle();
			}
		},
		house(data) {
			// [{'time':'2021-09-18','sales_volume':200},{'time':'2021-09-18','sales_volume':200}]
			const columnPlot = new Column('container', {
				data,
				xField: 'time',
				yField: 'sales_valume',
				height: 400,
				padding: [10, 50, 30, 50],
				label: {
					// 可手动配置 label 数据标签位置
					position: 'middle',
					// 配置样式
					style: {
						fill: '#FFFFFF',
						opacity: 0.6
					}
				},
				xAxis: {
					label: {
						autoHide: true,
						autoRotate: false
					}
				},
				meta: {
					time: {
						alias: '时间'
					},
					sales_valume: {
						alias: '销售额'
					}
				}
			});
			columnPlot.render();			
		},
		house_view(data){
			// const data = [
			//   { type: '分类一', value: 27 },
			//   { type: '分类二', value: 25 },
			//   { type: '分类三', value: 18 },
			//   { type: '分类四', value: 15 },
			//   { type: '分类五', value: 10 },
			//   { type: '其他', value: 5 },
			// ];
			const piePlot = new Pie('container_one', {
			  appendPadding: 10,
			  data,
			  angleField: 'monthlysale',
			  colorField: 'name',
			  radius: 0.9,
			  label: {
			    type: 'inner',
			    offset: '-30%',
			    content: ({ percent }) => `${(percent * 100).toFixed(0)}%`,
			    style: {
			      fontSize: 14,
			      textAlign: 'center',
			    },
			  },
			  interactions: [{ type: 'element-selected' },{ type: 'element-active' }],
			});
			

			piePlot.render();
			}
	},
	mounted() {
		this.seven_days();
	}
};
</script>

<style scoped="scoped">

.column{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	overflow: hidden;
}
.columnPlot {
	flex: 3;
}
.piePlot {
	flex: 2;
}
.heading {
	font-size: 20px;
	font-weight: bold;
}
.house-view {
	background-color: #ffffff;
	height: 400px;
	border: 1px solid #ebeef5;
	border-radius: 3px;
	overflow: hidden;
	margin-top: 30px;
	margin-bottom: 30px;
	margin-right: 30px;
}
.house_plot {
	background-color: #ffffff;
	height: 400px;
	border: 1px solid #ebeef5;
	border-radius: 3px;
	overflow: hidden;
	margin-top: 30px;
	margin-bottom: 30px;
}
.house_line {
	
}
</style>
